import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ACTION } from 'src/app/enums/action.enum';

import { UserDonationComponent } from './user-donation.component';

const routes: Routes = [
  {
    path: '',
    component: UserDonationComponent,

    data: {
      permission: [
        ACTION.EDIT,
        ACTION.ADD
      ],
      title: "Category List"
    }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class UserDonationRoutingModule { }
